<template>
    <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <img :src="원룸들[누른거].image" />
      <h4>{{ 원룸들[누른거].title }}</h4>
      <p>{{ 원룸들[누른거].content }} 내용임</p>
      <!-- <input @input="month = $event.target.value"/> -->
      <input v-model="month" />
      <input type="range" min="1" max="12">
      <button @click="$emit('closeModal')">창닫기</button>
      <p> {{ month }}개월 선택함 : {{ 원룸들[누른거].price * month }} </p>
    </div>
  </div>
</template>
<script>
export default {
  name : 'ModalVue',
  data(){
    return{
      month : 1,
    }
  },
  watch : {
    month(data){
      if(isNaN(data) == true){
        alert('문자입력 하지 마시오');
        this.month = 1;
      }
      //moth를 글자로 입력하면 경고문을 띄운다.
    }
  },
  props : {
    원룸들 : Array,
    누른거 : Number,
    모달창열렸니 : Boolean
  },
  beforeUpdate(){
    if(this.month == 2){
      alert('2개월은너무적어요');
      this.month = 3
    }
  }
}
</script>
<style>
.black-bg{
  width: 100%; height: 100%;
  background: rgba(0,0,0,0.5);
  position: fixed; padding: 20px;
}
.white-bg{
  width: 100%; background: white;
  border-radius: 8px;
  padding: 20px;
}

</style>