<template>
  <div class="menu">
    <a v-for="(a,i) in 메뉴들" :key="i">{{ a }}</a>
    <!-- <a v-for="a in 메뉴들" :key="a">{{ a }}</a> -->
  </div>
  <!-- <div class="start" :class="{end : 모달창열렸니}"> -->
  <Transition name="fade">
    <ModalVue @closeModal="모달창열렸니 = false" :원룸들="원룸들" :누른거="누른거" :모달창열렸니="모달창열렸니"/>  
  </Transition>
  <!-- </div> -->
  
  <DiscountVue @할인율="할인율--" :할인율="할인율" v-if= "showDiscount == true && 할인율 >= 0" />


  <button @click="priceSort">가격순정렬</button>
  <button @click="priceAscSort">가격 높은 순 정렬</button>
  <button @click="charSort">가나다 순 정렬</button>
  <button @click="sortBack">원상복귀</button>
   <!-- <div v-for="(a,i) in 원룸들" :key="i">
    <img :src="a[i].image" class="room-img" >
    <h4 @click="모달창열렸니 = true; 누른거 = i;">{{ a[i].title }}</h4>
    <p>{{a[i].price}} 만원</p> 
  </div> -->
    <CardVue @openModal="모달창열렸니 = true; 누른거 = $event" :원룸=원룸들[i] v-for="(a,i) in 원룸들" :key="i" :모달창열렸니="모달창열렸니" />
  <!-- <CardVue :원룸="원룸들[0]" :누른거="누른거" :모달창열렸니="모달창열렸니"/> -->

 
</template>

<script>
import data from './assets/oneroom.js';
import DiscountVue from './components/DiscountComponent.vue';
import ModalVue from './components/ModalVue.vue';
import CardVue from './components/CardVue.vue';

export default {
  name: "App",
  data(){
    return{
      showDiscount : true,
      할인율 : 30,
      원룸들오리지널 : [...data],
      누른거 : 0,
      원룸들 : data,
      모달창열렸니 : false,
      신고수 : [0,0,0],
      price1:60,
      price2:70,
      메뉴들 : ['Home', 'Shop', 'About'],
      products : ['역삼동원룸','천호동원룸','마포구원룸'], 
    }
  },
  methods : {
    increase(){
      this.신고수 += 1;
    },
    priceSort(){
      this.원룸들.sort(function(a, b){
        return a.price - b.price;
      })
    },
    sortBack(){
      this.원룸들 = [...this.원룸들오리지널];
    },
    priceAscSort(){
      this.원룸들.sort(function(a,b) {
        return b.price - a.price;
      })
    },
    charSort(){
      this.원룸들.sort(function(a,b){
        if(a.title > b.title) return 1;
        else if (a.title < b.title) return -1;
        else return 0;
      })
    },
  },
  // mounted(){
  //     setTimeout(()=>{
  //       this.showDiscount = false;
  //     },2000)
  //   },
  components : {
    DiscountVue : DiscountVue,
    ModalVue : ModalVue,
    CardVue : CardVue,
  }
};
</script>

